






import { Component, Vue, Emit } from 'vue-property-decorator';
import { isLogin } from '@/service/index';

@Component
export default class Conversation extends Vue {
    @Emit()
    private signOut() {
        this.$store.state.AV.User.logOut();
        this.$router.push('/user');
    }
    created() {
        isLogin()
    }
}
